
.bookx {
  border-radius: 4px;
  margin-top: 96px;
  margin-top: 77px;
}

.bookx {
  border: 1px solid #ccc;
  height: 242px;
  width: 100%;
  float: left;
  margin-left: 3%;
  margin-top: 34px;
  margin-top: 67px;
  margin-top: 70px;
  margin-bottom: 32px;
  margin-bottom: 53px;
  overflow: hidden;
  padding: 10px;
}

.theadx {
  text-align: center;
  margin: 10px 0;
}

.tablex {
  display: table;
  width: 100%;
  border-top: 1px solid #e9e9e9;
}

.rowx {
  display: table-row;
  border-bottom: 1px solid #e9e9e9;
  margin: 27px 0px;
}

.c1 {
  padding: 6px;
  text-align: center;
  font: 16px/25px latoregular;
  color: #2a2d2e;
  font-weight: bold;
}

.c1, .c2 {
  display: table-cell;
  border-top: 1px solid #e9e9e9;
  border-bottom: 1px solid #e9e9e9;
}

.cardP {
  box-shadow: 0 4px 8px 0 rgba(0,0,0,0.2);
  transition: 0.3s;
  width: 98%;
  position: relative;
  /* backgdround-color: red; */
  border: 1px solid #FFA126;
  border-radius: 5px;
  cursor: pointer;
}

.cardP .topx {
  border-bottom: 1px solid #FFA126;
  border-radius: 0px;
  width: 100%;
  -moz-box-shadow: inset 0 -10px 10px -10px #000000;
  -webkit-box-shadow: inset 0 -10px 10px -10px #000000;
  box-shadow: inset 0 -10px 10px -10px #000000;
  -webkit-user-select: none;
  -khtml-user-select: none;
  -moz-user-select: none;
  -o-user-select: none;
  user-select: none;
}

.cardP img {
  display: table-cell;
  margin: 10px auto;
  -webkit-user-select: none;
  -khtml-user-select: none;
  -moz-user-select: none;
  -o-user-select: none;
  user-select: none;
}

/* .cardP img {
  vertical-align: middle;
} */

.tickx {
  margin-right: 15px;
  margin-top: 5px;
  background: #8cb807 url(../../img/whitetick.png) no-repeat 7px 5px!important;
  text-indent: -999px;
  padding: 0 5px;
  width: 17px;
  height: 23px;
}

.theadx>h5 {
  font: 19px/25px patua_oneregular;
  color: var(--dark);
  padding: 6px 0px;
}

small, .small {
  display: block;
  font-size: 10px;
  font-family: monospace;
  line-height: 1em;
  margin-top: -4px;
  margin-bottom: 10px;
}


.tablex .c1 p {
  color: var(--dark);
/*   font-family: 'latoblack'; */
  font-size: 15px;
}

.save-btn-icon {
  float: left;
  border-radius: 3px;
  font: 14px/26px open_sansregular;
  color: #FFF;
  display: block;
  padding: 5px 12px;
}

.cancel-btn {
  color: #2588bd !important;
  font: 12px/26px open_sansregular;
  padding-top: 3px;
  cursor: pointer;
}

.edit-bt {
  float: right;
}

.cardP .container {
  border: 1px solid #FFA126;
  width: 100%;
  background: #FFA126;
}

.container span.boxer-txt {
  width: 75%;
  text-align: center;
  background-color: #FFA126;
  color: #fff;
  font-size: 13px;
  font-family: 'latoblack';
}

.all-visible {
  display: block;
}

