.guide-profile-mid-container .guide-profile-right {
  margin-top: -50px;
}

.guide-profile-mid-container .guide-profile-right {
  margin-top: 5px;
  float: right;
}

.guide-profile-mid-container .guide-profile-right {
  width: 100%;
  float: right;
}

.your-profile-complete {
  padding: 0;
  margin: 0 0 30px;
  display: inline-block;
  width: 100%;
}

.your-profile-complete .complete_box {
  margin: 24px 0 0px 0;
}

.your-profile-complete h5 {
  /*background: url(../images/line-icon.jpg) no-repeat left center;*/
  border-bottom: 1px solid #e5e5e5;
  font: 19px/40px patua_oneregular;
  color: #2a2d2e;
  padding: 0 0 0 12px;
}

.your-profile-complete p {
  font: 14px/24px proxima_nova_rgregular;
  color: #4d5153;
  padding: 10px 0 0;
}

.prog-section .ui-widget-content {
  background: #e4e4e4;
  border: none;
}

.ui-widget {
  font-size: 13px;
}

.ui-progressbar {
  height: 2em;
  text-align: left;
}

.ui-corner-all {
  -moz-border-radius: 4px;
  -webkit-border-radius: 4px;
  border-radius: 4px;
}

.ui-widget-content {
  border: 1px solid #aaa;
  background: #fff; /*url(images/ui-bg_flat_75_ffffff_40x100.png) 50% 50% repeat-x;*/
  color: #222;
}

.ui-widget {
  font-family: Verdana,Arial,sans-serif;
  font-size: 1.1em;
}

.prog-section .ui-progressbar .ui-widget-header {
  background: #8ab53f;
  border: none;
}

.ui-progressbar .ui-progressbar-value {
  margin: -1px;
  height: 100%;
width: 25%;
}
