#cal_detail{width: 100%; float: left;  border-radius:3px; padding:10px 0 0 0; margin-top:15px;}
#cal_detail table{width:100%; border:1px solid #CCCCCC; padding-top:10px;}
#cal_detail table tr td{backdground:#f9fafb; font:14px/20px proxima_nova_rgregular; colodr:#abbcc4; text-align:center; border-top:1px solid #cccccc; border-right:1px solid #cccccc; padding:7px;}
#cal_detail table tr.head_day td{background:#fff; border:none; color:#151212;}
#cal_detail table tr.head_day{border-bottom:1px solid #e0e0e0;}
#cal_detail table tr th a.prev{/*background:url(../images/calender-prev.png) left center no-repeat;*/ width:9px; height:17px; float:right;}
#cal_detail table tr th span{font:16px/30px proxima_nova_rgregular; color:#000;}
#cal_detail table tr th a.next{/*background:url(../images/calender-next.png) right center no-repeat;*/ width:9px; height:17px; float:left;}
#cal_detail table tr td .today{background:#fff; font-weight:bold; padding:7px 0; color:#000;}
#cal_detail table tr td.td_today{padding:0px;}
#cal_detail table tr td.afterday{background:#fff; font-weight: normal; padding:7px 0; color:#000;}
#cal_detail .c_v_detail{color:#2588bd; font:14px/30px proxima_nova_rgregular;  /*background: url(../images/more-bt-2.png) no-repeat  145px 11px;*/ position: relative;  text-transform: capitalize; display:block }
#cal_detail table tr td.datablue{background:#2588bd; color:#fff;}
#cal_detail table tr td.datablue a{color:#fff;}
.c_div_span {position: absolute; width:100px; color: #000000; font: 12px/14px 'latobold';top:-45px;background: #fff;border: 2px solid #f0f0f0;
height:auto; line-height: 30px;text-align: center;visibility: hidden;border-radius: 6px; padding:0 10px; left:20%;margin-left: -50px;}
.c_div{position:relative;}
.c_div_span:before {content: ''; position: absolute; top: 100%; left: 50%; margin-left: -12px; width: 0; height: 0;border-top: 12px solid #f0f0f0;
border-right: 12px solid transparent; border-left: 12px solid transparent;}
.c_div_span:after {content: ''; position: absolute; top: 100%; left: 50%; margin-left: -8px;width: 0; height: 0;border-top: 8px solid #FFFFFF;
border-right: 8px solid transparent; border-left: 8px solid transparent;}
.c_div:hover .c_div_span {visibility: visible;opacity: 1;bottom: 30px;left:20%;margin-left: -50px;z-index: 999;}
#cal_detail table tr td.dataorange{background:#ccc; color:#fff;}
#cal_detail table tr td.dataorange a{color:#fff;}
.cal_detail_pub table tr td.datablue{background: #ccc none repeat scroll 0 0 !important;color: #fff !important;}

#cal_detail table tr td .c_div {
    position: relative;
    background: #f9fafb;
}
#cal_detail table tr td .c_div a
{
	background: #f9fafb;
	color: #abbcc4;
}