.other-contact-details-panel ul {
  width: 90%;
  margin: 0 auto
}
.other-contact-details-panel ul li {
  font: 14px/25px;
  display: inline-block;
  width: 100%;
  padding: 0;
  position: relative;
}

.other-contact-details-panel ul li {
  padding: 10px 0;
  position: relative;
  text-transform: capitalize;
  height: 31px;
}

.other-contact-details-panel ul li img {
  float: left;
  padding: 0 2px 0 0
}

.other-contact-details-panel ul li span {
  float: right !important;
  padding: 0;
  width: auto !important
}

.other-contact-details-panel ul li span img {
  padding: 0 0 0 10px
}

.other-contact-details-panel ul li p {
  display: none
/*   color: var(--default) */
}

.other-contact-details-panel ul li p {
  width: 70%
}

.other-contact-details-panel ul li p {
  float: left;
  
}

.other-contact-details-panel ul li:hover p {
  display: block;
  padding-top: 8px;
  padding-left: 8px;
  color: var(--dark);
/*   font-weight: bold; */
}

.other-contact-details-panel div img {
  vertical-align: middle;
}
