.traveler_rating{ background:#fff; width:251px;  margin-bottom:20px;}
.traveler_rating h3{ color:#6c6f72;
    font-size: 18px; float:left;
    line-height: 23px; padding:0;}
.overall_rating{ float:right;} 
.traveler_rating h4{ color:#2785b8; font:16px/32px proxima_nova_rgregular; border-bottom:1px solid #efefef; padding:10px 0 0 0; }
.rating_wrap{ width:auto;}
.rating_type { width:100%; margin-top:15px;}
.rating_type p{color:#6c6f72;  font-size: 14px; float:left; min-width:75px; line-height:12px; text-transform:capitalize;}
.rating_type p a{color:#6c6f72; text-decoration:none;}
.rating_bg{ background:#eaecec; width:150px; height:16px; float:left;}
.rating_bg span{ background:#8ab53f; width:75px; height:16px; float:left;}
.rating_type small{color:#6c6f72; font-size: 14px; text-align:center; line-height:12px; margin:0 0 0 6px;}
.rating_bg span{ background:#8ab53f; width:75px; height:16px; float:left;}
.traveler_rating {
    width: 300px;
  }
  .traveler_rating {
    background: #fff;
    width: 251px;
    margin-bottom: 20px;
  }
  .traveler_rating h3 {
    width: 100%;
    background: none;
    text-align: center;
    padding: 0px;
  }

  .traveler_rating h3 {
    /* background: url(../images/line-icon.jpg) no-repeat left center; */
    font: 18px/25px patua_oneregular;
    color: #2a2d2e;
    padding: 0 0 0 12px;
  }

  .traveler_rating h3 {
    color: #6c6f72;
    font-size: 18px;
    float: left;
    line-height: 23px;
    padding: 0;
  }

  .ratingx .traveler_rating h3:after {
    background: #2688bd;
    width: 62px;
    height: 2px;
    text-align: center;
    margin: 0 auto;
    left: 50%;
    /* margin-left: -31px; */
    margin-bottom: 24px;
    margin-top: 8px;
    content: "";
    display: block;
  }

  .ratingx {
    clear: both;
    padding-top: 30px;
  }

  .ratingx .traveler_rating h4 {
    width: 100%;
    text-align: center;
    margin-bottom: 0px;
  }

  .ratingx h4 {
    border-bottom: none !important;
    font: 15px/30px !important;
    line-height: 1.5em !important;
    padding: 0px !important;
  }