.ProfileHeader {
    width: 100%;
    height: 196px;
    flex-direction:column;
    align-items: center;
    gap: 8px;
    margin: 10px auto;
    padding: 16px 0;
  }
  
  .ProfileHeader::after {
    content: "";
    width: 100%;
    border-bottom: 1px solid #ccc;
  }
  
  div#userName {
    color: var(--light);
    font-weight: 700;
    font-size: .825rem;
    word-spacing: .25rem;
  }

div#userName a {
  background-color: var(--dark);
  padding: 4px 8px;
  color: var(--light);
  border-radius: 4px
}
  
.profile-container .user-profile {
   /* flex-direction: column; */
   align-items: baseline;
   /* gap: 5px; */
   justify-content: center;
}
  
   .user-img {
    display: inline-flex;
    border: 4px solid var(--dark);
    border-radius: 50%;
    position: relative;
    justify-content: center;
    align-items: center;
    margin-right: 8px;
  }
  
  .profile-container img {
    background-color: #dd1c1c5e;
    width: 72px;
    height:72px;
    border-radius: 50%;
    color: var(--light);
    font-size: .5rem;
    
  }

  .profile-content {
   margin: 0 auto;
    padding: 0 15px;
    display: flex;
    flex-wrap: wrap;
  }

/* Adjusting btn and btn-submit for this page.*/
.ProfileHeader .profile-actions {
	text-align: center;
	gap: 6px;
}

.ProfileHeader .profile-actions .MediumWrap {
	margin-right: 16px;
}

.ProfileHeader .profile-actions .SmallWrap {
    padding: 0;
}

.ProfileHeader .btn,
.ProfileHeader .btn-submit {
	padding: 4px 8px;
width: 100%;

} 


  .informations {
    width: 100%;
    position: relative;
    margin-bottom: 8px;
  }
  
  .informations > div {
   margin: 16px 0;
   width: 100%;
   position: relative;
  }
  
  .informations > div::after {
    content: "";
    width: 100%;
    border-bottom: 1px solid #ccc;
  }
  
  .informations h4 {
    color: var(--dark);
    margin-bottom: 4px;
    font-size: 1rem;
  }
  
  .informations .bio p {
    font-size: .825rem;
    text-indent: 1.5rem;
  }
  
  
  .posts {
   width: 100%;
   margin-bottom: 10px;
   padding: 8px;
   border: 1px solid #ccc;
   border-radius: 8px
  }
  
  
  .posted {
    width: 100%;
   margin-bottom: 10px;
   padding: 8px;
  }
  
  .post-head {
   margin-bottom: 8px;
   display: flex;
   flex-wrap: wrap;
   align-items: center;
   font-weight: bold;
   font-size: .825rem;
  
   border-top: 1px solid #ccc;
    border-bottom: 1px solid #ccc;
  }
  .post-head img {
    width: 28px;
    height: 28px;
    color: #fff;
    font-size: .5rem
  }
  
  .post-head .user-name,
  .ph-text{
   padding: 0 10px
  }
  
  .post-head .ph-text span {
   display: block;
  }
  
.post-head .ph-text {
    width: calc(100% - 38px);
    margin-bottom: 8px;
    display: flex;
    flex-wrap: wrap;
    font-weight: bold;
    font-size: .825rem;
} 
  
.post-head .ph-text span:first-child:after{
    content: "|";
    margin: 0 8px;
    height: 3.825rem;
  }
  
  
@media (min-width: 768px){
  
   .ProfileHeader {
      gap: 0;
      flex-direction: row;
      justify-content: space-between;
      align-items: flex-end;
      border: 1px solid #ccc;
      border-radius: 8px
    }
   
   .ProfileHeader::after {
      display: none;
    }
   
   .profile-content {
      padding: 0;
    }

   .informations {
      padding-left: 0;
    }

   .ProfileHeader .profile-actions {
      flex-direction: row-reverse;
      align-items: flex-start;
      justify-content: end;
    }
}