
/*  ANNOTATIONS
  CLASSES:
  -- row -> container-flex;
  -- col-12 -> container-large || wrap-large;
      width: 100% of contaiiner
  -- col-md-9 -> 3 * quarter
      wrap-3q
  -- col-md-6 -> container-medium
    Abreviation: container-md || wrap-md
    width: 50% of container
  -- col-3: quater container
    Abreviation: container-small
    width: 25% of container

  -- OBS: in mobile all container width will be settup to
          100% of width.

*/
.Settings {
  --header-height: 72px;
  --bg-default: #92c2f9;
  --bg-on-hover: #8ab53f;
  --primary-font-color: ##2a2d2e;
  
  /* #2c2324;*/
}

.container-navbar {
  /* Inicio Navbar */
  box-shadow: -1px -1px 6px #808080;
}

/****  General Settigs  ****/
.Settings {
  margin: 0;
  font-family: Verdana, Geneva, Tahoma, sans-serif;
  font-size: 0.875rem;
  font-weight: 400;
  line-height: 1.5;
  color: var(--primary-font-color);
  text-align: left;
  min-height: 100vh;
/*       background-color: #e4e5e6;  */
}

a {
  color: var(--primary-font-color);
  text-decoration: none;
}

main {
  min-height: calc(100vh - var(--header-height));
  flex: 1 1;
  position: relative;
}

/* SettingHeade */
.Settings .row {
  margin-left: auto;
  margin-right: auto;
  margin-top:16px;
  margin-bottom: 20px;
}

.Settings .row:after {
  content: "";
  display: table;
  clear: both;
}

.Settings h5 {
  font-size: 1.64rem;
  line-height: 110%;
  margin: 1.0933333333rem 0 .656rem 0;
}

.Settings h5 a{
  padding-left: .75rem;
  font-weight: inherit;
}

.Settings h5 a {
  color: var(--dark);
  text-decoration: none;
  -webkit-tap-highlight-color: transparent;
}

.Settings #accountText {
  margin: 0 auto;
}

.Settings #accountText p {
  text-indent: 1.5rem;
}


.Settings img {
  display: inline-block;
  margin-left: 1.25rem;
  width: 3.125rem
}

/* Im Mobile-first: Nav is row */
.Settings nav {
  margin-bottom: 0;
  list-style: none;
}

.side {
  margin-top: 5px;
  border: 1px solid #e4e7ea;
  border-radius: 8px;
}

.nav-item {
  flex: 1 1;
  text-align: center;
    /* clear: both; */
}

.nav-item {
  /* padding: 3px 1rem; */
  min-width: 180px;
}

.nav-profile .nav-link {
  display: block;
  padding: 0.5rem 1rem;
  border-radius: 4px;
  margin-bottom: 1rem;
  transition: background-color 0.25s ease-in-out;
  width: 100%;
}

.nav-profile .nav-item .active {
  background-color: var(--dark);
  color: var(--light);
  font-weight: 600;
  position: relative
  
}

.nav-profile .nav-item .active:after {
  content: "";
  display: inline;
  height: 38px;
  margin-right: -8px;
  border: 4px solid var(--bg-on-hover);
  right: 0;
  top: -2px;
  position: absolute;
}

.nav-profile .nav-link:hover,
.nav-profile .nav-item .active:hover {
  background-color: var(--bg-on-hover);
  color: var(--light)
}

.Settings  .nav{
  margin-top: 2rem;
}

.tab-panel {
  padding: 1.5rem .125rem;
  max-width: 992px;
}


/**** CSS Form and Input settings ****/
.Settings .form-group {
  margin-bottom: 1.625rem;
}

.form-group input,
.btn,
.form-control {
  background-clip: padding-box;
  background-color: #fff;
  border: 1px solid #e4e7ea;
  border-radius: 0.25rem;
  color: #5c6873;
  display: block;
  font-size: 0.875rem;
  font-weight: 400;
  height: calc(1.5em + 0.75rem + 2px);
  line-height: 1.5;
  padding: 0.375rem 0.75rem;
  transition: border-color 0.15s ease-in-out, box-shadow 0.15s ease-in-out;
  width: 100%;
}


.form-group label {
  font-weight: 600;
  margin-bottom: 0.5rem;
  display: inline-block;
}

.btn {
  color: var(--primary-font-color);
  /*   font-weight: 600; */
}

.Settings .form-btn {
  margin-top: 1.5rem;
  justify-content: center;
}

.form-btn > .wrap-small {
  width: 75%;
  margin-bottom: 1rem;
}

.btn-submit {
  font-family: "Graphik", serif;
  font-weight: 600;
  font-style: normal;
  line-height: 1.5;
  color: #347cb4;
  background-color: transparent;
  border: 1px solid #347cb4;
  transition: 0.3s;
}

.btn-submit:hover {
  background-color: #347cb4;
  color: #fff;
  cursor: pointer;
}
/**** CSS Form and Input END ****/


/****  CSS show/hide element with :target pseudo-class ****/
/*#basicInfo,
#legalInfo,
#notifyAndPrivacy,
#security {
  display: none;
}*/

:is(#basicInfo,
    #legalInfo,
    #notifyAndPrivacy,
    #security
  ):target {
  display: block;
}
/**** END CSS show/hide ****/

/****  MEDIAS *****/
@media (min-width: 768px) {
  .nav {
    flex-direction: column;
  }

  .nav-item {
    min-width: auto;
    text-align: right;
  }

  .nav-profile .nav-link {
/*     margin-top: 40px; */
    padding: 10px;
    border-right: 3px solid transparent;
/*     font-weight: 600; */

  }

  .tab-pane {
    /* padding-right: 1.5rem; */
    margin-right: calc(2 * 8.3336%);
  }

  .wrap-large {
    flex: 0 0 100%;
    max-width: 100%;
  }

  .wrap-md {
    flex: 0 0 50%;
    max-width: 50%;
  }

  .wrap-small {
    flex: 0 0 25%;
    max-width: 25%;
  }

  .wrap-3q {
    flex: 0 0 75%;
    max-width: 75%;
  }

  .tab-content .form-btn {
    flex-direction: row-reverse;
    align-items: flex-start;
    justify-content: end;
  }
}
